export class Vector3 {
    x: number = 0;
    y: number = 0;
    z: number = 0;

    set(x?: number, y?: number, z?: number) {
        if (x !== undefined) {
            this.x = x;
        }

        if (y !== undefined) {
            this.y = y;
        }

        if (z !== undefined) {
            this.z = z;
        }

    }

    clear() {
        this.x = 0;
        this.y = 0;
        this.z = 0;
    }
}